export default {
	data() {
		return {
			res:"",
			user:"",
			pwd:"",
			userCode:''
		}
	},
	methods: {	
		denglu:function() {
			var that=this;
			uni.request(
			{
				url:'http://192.168.1.143:8080/Users/login',
				method:'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded', 
				},				
				data:{userName:that.user,passWord:that.pwd},
				success:function(res){
					console.log(res);
					that.userCode=res.data.userCode;
					if(res.data=="yonghubucunzai"){
						uni.showModal({title:"不存在！"})
					}else{
						if(res.data=="mimacuowu"){
							uni.showModal({title:"密码错误！"})
						}
						else{
							uni.switchTab({
								url:'../index/index'
							})
							uni.setStorage({
								  key: 'storage_name',
								  data: that.user,
							})
							uni.setStorage({
								key:"storage_pwd",
								data:that.pwd,
							})
							uni.setStorage({
								key:"storage_nameCode",
								data:that.userCode,
							})
						}
					}
					}
				}
			
			)
		}
}
}
